@import "./../base/vars";
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__link {
    font-family: Lato SemiBold;
    font-weight: 500;
    color: $accentColor;
    font-size: 20px;
    display: flex;
    @media all and (max-width: 1182px) {
      font-size: 18px;
    }
    @media all and (max-width: 1030px) {
      font-size: 16px;
    }
  }
  &__item {
    margin-left: 30px;
    @media all and (max-width: 1148px) {
      margin-left: 20px;
    }
  }
  &__list {
    display: flex;
  }
  &__logo {
    max-width: 270px;
    width: 100%;
  }
}
