@import "./../base/vars";
.phone {
  font-size: 40px;
  font-family: Lato-Black;
  @media all and (max-width: 980px) {
    font-size: 30px;
  }
}
.header {
  &__phone {
    color: $accentColor;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }
  &__phone__link {
    font-size: 24px;
    border-bottom: solid 1px $accentColor;
    font-family: Lato-Ligh;
  }
  &__title {
    font-family: Lato-Regular;
    font-size: 60px;
    line-height: 130%;
  }
  &__title--srong {
    font-size: 72px;
  }
  &__wrap {
    max-width: 622px;
    width: 100%;
  }
}
